.letterContainer {
  font-family: "Roboto", sans-serif;
  display: flex;
  font-size: 100%;
  margin-left: 1%;
}
.linkContainer {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  justify-content: flex-end;
  gap: 18px;
  margin: 20px;
  font-size: small;
  font-family: "Roboto", sans-serif;
}
.search-box {
  margin-top: 1%;
  margin-left: 3%;
  flex: 2;
}
.tabsContainer {
  font-family: "Roboto", sans-serif;
}
.horizontal-line {
  width: 120%;
  margin-left: -10%;
  margin-top: -1px;
  height: 0.5pt;
  background-color: #c1c1c1;
  border: none;
}
.searchBoxContainer {
  display: flex;
  top: 0;
  flex: 1;
  flex-direction: row;
}
.textFieldOpen fieldset {
  border-top-right-radius: 30pt;
  border-top-left-radius: 30pt;
  border-color: #696969;
}
.textFieldClose fieldset {
  border-radius: 30pt;
  border-color: #696969;
}
.letterD {
  color: #4885ed;
}
.letterO {
  color: #db3236;
}
.letterH {
  color: #f4c20d;
}
.letterY {
  color: #4885ed;
}
.letterU {
  color: #3cba54;
}
.letterN {
  color: #db3236;
}
